import Typography from 'components/ui/Typography';
import Picker from 'components/ui/Picker';
import { InvestBias } from '@package/shared-consts';

export interface QuestionProps extends InvestBias.Questions {
  initialSelect: number[];
  onUpdateSelected: (selected: number[]) => void;
}

const Question = ({
  question,
  multiselectYn,
  items,
  initialSelect,
  onUpdateSelected
}: QuestionProps) => {
  return (
    <div className="flex flex-col gap-y-6 w-full">
      <div className="flex flex-col min-h-[4.5rem] py-3 px-5 max-tablet:py-4">
        <Typography align="center" weight={700} color="text-gray-800">
          {question}
        </Typography>
      </div>
      <div className="flex w-full px-5">
        <Picker
          shouldMultiCheck={multiselectYn === 'Y'}
          list={items.map(({ itemNo, itemContent, itemScore }) => ({
            key: itemNo,
            label: itemContent,
            value: itemScore.toString()
          }))}
          initialSelect={initialSelect}
          onUpdateSelected={onUpdateSelected}
        />
      </div>
    </div>
  );
};

export default Question;
